body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.nav-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background: #525252;
  position: relative;
}

.nav-item {
  color: rgba(255, 255, 255, 0.5);
  width: 90px;
  height: 60px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
  cursor: pointer;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  z-index: 1;
}

.nav-item:hover {
  color: white;
}

.nav-item-indicator {
  width: 10px;
  height: 100%;
  position: absolute;
  left: -10px;
}

.hover-element {
  position: absolute;
  height: 60px;
  width: 100%;
  background: rgba(255, 255, 255, 0.5);
  pointer-events: none;
}



